@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  font-family: "Open Sans", serif;
}


.wrapper {
  background: -webkit-linear-gradient(left, #e67e22,#3ca8e7);
  background: linear-gradient(to right, #e67e22,#3ca8e7);
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.main {
  height: 90vh;
  background: #fff;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 80%;
  margin: 0 auto;
}

.home-container {
  height: 90vh;
  background: url(/static/react/media/cloudy-sky.ff0abf55.jpg) center center no-repeat;
  background-size: cover;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  color: #2C3E50;
}

.home-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.home-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.form-container {
  background-color: #3f556b;
  height: 90vh;
  padding-top: 100px;
  padding-left: 50px;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f19551;
  width: 30%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}


button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f19551;
  color: #fff;
  font-weight: 100;
}

button:active {
  outline: none;
}

.weather__info {
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
  text-align: center;
}

.weather__city {
  padding: 20px 0 20px 0;
  color: #f19551;
  font-weight: bolder;
  font-size: 26px;
  border-bottom: solid 2px rgba(255,255,255,0.06);

}
.weather__key {
  color: #f19551;
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.error {
  color: #f71c03;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}


@media only screen and (max-width: 768px) {
    .wrapper {
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      height: 100%;
    }
    .main {
      height: 90vh;
      margin: 0px;
      width: 100%;
    }
  
    .container {
      width: 100%;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      padding-right: 0px; 
      padding-left: 0px; 
      margin-right: 0px;
      margin-left: 0px;

    }
    .row {
      width: 100%;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      margin-right: 0px; 
      margin-left: 0px; 

    }
    .home-container {
      width: 100%;

    }

      .col-xs-7 {
        width: 100%;
        padding-left: 0px;
      }
      
      form {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        margin-left: 15px;
      }
      input[type="text"] {
        border: solid 1px #f19551;
        width: 100%;
        
        
      }
    
}
